
.comments {
  margin-top: 60px;
  background: #fff;
  padding: 6px 12px;
  overflow: hidden;
}
.title {
  font-size: 1.5em;
  color: #333;
}
.loader-margin {
  margin: calc(50vh - 60px) auto;
}
.list-unstyled {
  list-style: none;
  padding-left: 0;
}
.comment-info {
  padding-bottom: 12px;
  border-bottom: 1px solid #ccc;
}



